      const myVideo = document.getElementById('indexVideo')
      if (myVideo.play) {
        myVideo.play()
      }
    }, 5000)
  },
  methods: {
    itemClick (val) {
      if (val === 1) {
        this.$router.push('/shuaShuaShua')
      } else if (val === 2) {
        this.$router.push('/letFeedDragon')
      } else if (val === 3) {
        this.$router.push('/fightWithTeacher')
      } else if (val === 4) {
        this.$router.push('/getTickets')
      } else if (val === 5) {
        this.$router.push('/goddessAuth')
      }
    },
    waitingForIt () {
      this.$message.info('敬请期待')
    },
    getTime () {
      if (new Date().getHours() < 14) {
        return true
      } else {
        return false
      }
    },
    menuClick (val) {
      this.$router.push(val)
    }
  }
}
